import { ContentBlockTypes } from "@rp/react/rpjson/types"
import { EnhancedBlock, EnhancedDocumentTypes } from "@rp/react/rpjson/types"
import { TAG_NAMES } from "../constants"

export const getTagElement = (type: string | undefined) => {
  switch (type) {
    case EnhancedDocumentTypes.PAGE:
      return TAG_NAMES.DIV
    case EnhancedDocumentTypes.PARAGRAPH:
      return TAG_NAMES.P
    case ContentBlockTypes.HEADING:
      return TAG_NAMES.H3
    case ContentBlockTypes.HEADER:
      return TAG_NAMES.HEADER
    case ContentBlockTypes.FOOTER:
      return TAG_NAMES.FOOTER
    case ContentBlockTypes.TABLE:
      return TAG_NAMES.TABLE
    case ContentBlockTypes.FORM:
      return TAG_NAMES.FORM
    case ContentBlockTypes.LIST_UNORDERED:
      return TAG_NAMES.UL
    case ContentBlockTypes.LIST_ORDERED:
      return TAG_NAMES.OL
    case EnhancedDocumentTypes.LIST_ITEM:
      return TAG_NAMES.LI
    case EnhancedDocumentTypes.TABLE_ROW:
      return TAG_NAMES.TR
    case EnhancedDocumentTypes.TABLE_CELL:
      return TAG_NAMES.TD
    case EnhancedDocumentTypes.CONTAINER:
      return TAG_NAMES.DIV
    default:
      return TAG_NAMES.P
  }
}

export const getChildrenTagName = (parent: EnhancedBlock) => {
  if (parent.type === ContentBlockTypes.FORM) return TAG_NAMES.DIV
  if (parent.type === ContentBlockTypes.LIST_UNORDERED) return TAG_NAMES.LI
  if (parent.type === ContentBlockTypes.LIST_ORDERED) return TAG_NAMES.LI
  return undefined
}
